import React, { Component } from 'react';

class Rolls extends Component {
    constructor(props) {
        super(props);
        this.keys = ["STR", "DEX", "CON", "INT", "WIS", "CHA", "HEALTH"]
        this.tooltips = ["Strength",
                         "Dexteriry",
                         "Constitution",
                         "Intelligence",
                         "Wisdom",
                         "Charisma",
                         "Rolled health excluding CON modifier"]

        this.rolls = ["--", "--", "--", "--", "--", "--"];
        this.health = "--";
    }
    render() {
        console.log(this.props.rolled)
        if (this.props.rolled) {
            this.rolls = this.props.rolls;
            this.health = this.props.health;
        }
        console.log(this.rolls)
        const ability_rolls = []
        for (var i = 0; i<6; i++){
            if (!this.props.isMobile){
                var tooltip = <span className="tooltiptext">{this.tooltips[i]}</span>
            }
            ability_rolls.push(
                <div className="stat-block">
                    <div className="stat">
                         {this.rolls[i]}
                    </div>
                    <div className={"lab-" + this.keys[i].toLowerCase()}>{this.keys[i]}
                    {tooltip}
                    </div>
                </div>
            );
        }   
        if (!this.props.isMobile){
            var tooltip = <span className="tooltiptext">{this.tooltips[6]}</span>
        }
        ability_rolls.push(
            <div className="stat-block">
                <div className="stat">
                     {this.health}
                </div>
                <div className="lab-health"> HP
                {tooltip}
                </div>
                
            </div>
        );
        const cls_name = this.props.isMobile ? "stats-mobile" : "stats"
        return (
            <div className={cls_name}>
                {ability_rolls}
            </div>
        );
    }
}

export default Rolls;